  font-size: 16px;
}

.nav-link {
  color: var(--text-color);
  padding: 4px;
  font-size: 16px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  position: relative;
  transform: scale(1);
}

.nav-link:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: var(--text-color);
  transform-origin: 0;
  transition: transform .3s ease-in-out;
  position: absolute;
  bottom: -3px;
  left: 0;
  transform: scaleX(0);
}

.nav-link:hover:after {
  transform: scaleX(1);
}

